html, body {
  height: 100vh !important;
  min-width: 830px;
}

label {
  color: white;
}

body {
  background-image: url('../images/background2.jpg') !important;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: none;
  color: rgb(65, 65, 65) !important;
  padding-top: 65px;
  margin-top: -65px;
}
#Header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.imageLabelDiv {
  display: inline-block;
}
.accountTypeText {
  color: white;
  cursor: pointer;
  border: solid white 1px;
  margin-bottom: .3rem;
  padding: .2rem .5rem;
}
.accountTypeText:hover {
  text-decoration: underline;
}

.formParent {
  background-color: rgb(0, 0, 0, .8) !important;
  padding: 30px 40px;
  width: min-content;
  height: min-content;
  margin-left: auto;
  margin-right: auto;
}

.inputField {
  background-color: white;
  color:rgb(58, 58, 58);
  border-radius: 2px;
  border: solid rgb(173, 173, 173) 1px;
  font-size: 16px !important;
  padding: 5px 5px;
  margin: 8px 5px;
  line-height: normal;
}
select {
  cursor: pointer;
}
option .initialSelect {
  color: rgb(173, 173, 173) !important;
}
.inputField:focus {
  outline: none;
  border: solid rgb(167, 167, 167) 1px;
}
a {
  color: rgb(255, 255, 255) !important;
  font-size: 1.1rem !important;
}

.errorMessage {
  background-color: rgb(255, 206, 206);
  padding: 3px 15px !important;
  margin: 4px !important;
  margin-left: auto !important;
  margin-right: auto !important;
  width: max-content;
  color: rgb(255, 0, 0);
  border: dashed 1px;
}

#userForm .inputField, #editUserForm .inputField, #ticketForm .inputField, #editTicketForm .inputField, #projectForm .inputField, #editProjectForm .inputField {
  background-color: white;
  border-radius: 2px;
  width: 40% !important;
}
#userForm .wideInputField, #ticketForm .wideInputField, #editTicketForm .wideInputField, #editUserForm .wideInputField, #projectForm .wideInputField, #editProjectForm .wideInputField, #editUserForm .wideInputField {
  width: 83% !important;
  margin-left: auto !important;
  margin-right: auto !important;
}

.splashMessage {
  width: max-content !important;
  margin-left: auto !important;
  margin-right: auto !important;
  font-size: 1.4rem;
  font-weight: 500;
  color: white !important;
}
.smallText {
  font-size: 1.2rem;

  font-weight: 400 !important;
  color: white !important;
}
#userImageDiv {
  width: max-content;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
}

.listContainer {
  margin: 20px !important;
}

.addNewItem {
  text-decoration: underline;
  padding: 5px;
}

.tableContainer {
  width: 85% !important;
  margin-left: auto;
  margin-right: auto;
  height: 35vh;
  border-radius: 0 !important;
  background-color: rgb(255, 255, 255, .9) !important;
}
#editsHistoryDisplay {
  margin: auto;
  margin-top: 5vh;
}
.tableContainerLarge {
  box-shadow: 0px 3px 10px 0px rgb(122, 122, 122) !important;
  width: 85% !important;
  padding: 10px !important;
  margin-top: auto !important;
  margin-bottom: auto !important;
  margin-left: auto;
  margin-right: auto;
  height: 80vh;
}

.isCentered {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100vh !important;
  position: fixed !important;
  left: auto;
}

.isCentered2 {
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.addNewButton {
  background-color: #ffffff !important;
  border: none;
  border-radius: 2px;
  margin-top: auto;
  margin-bottom: auto;
  padding: 5px !important;
  height: min-content;
  cursor: pointer;
  color: rgb(0, 0, 0) !important;
  font-weight: 600 !important;
}

.authButton {
  margin-top: 10px !important;
  margin-bottom: 5px !important;
  margin-left: 5px !important;
  margin-right: 5px !important;
  width: 180px;
}
.deleteButton {
  background-color: rgba(245, 121, 121, 0.9) !important;
  color: white !important;
}
.isNotComplete {
  background-color: rgba(108, 146, 228, 0.9) !important;
  color: white !important;
}
.isComplete {
  background-color: rgba(43, 150, 43, 0.9) !important;
  color: white !important;
}
.tallInput {
  height: 100px !important;
  text-align: left !important;
  background-color: white !important;
  font-family: inherit;
}
.editButtonGreen {
  color:rgb(17, 17, 17) !important;
  cursor: pointer !important;
}
.editButtonGrey {
  color: grey !important;
  cursor: default !important;
}
.bold {
  font-weight: bold;
}
a {
  color: rgb(250, 250, 250) !important;
}
a:hover {
  cursor: pointer;
}

.welcomeMessage {
  color: rgb(255, 255, 255) !important;
  margin: 0 !important;
  padding: 1rem;
  border-radius: 0;
  font-weight: 500;
}

h2 {
  margin: 10px !important;
}

.sectionName {
  color: white;
  font-size: 1.4rem;
  line-height: 24px;
  padding: 10px 0;
}
.sectionTopBar {
  background-color: rgb(0, 0, 0, .9);
  padding: 0px 10px;
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  width: 85%;
  height: 60px;
}
.sectionBottomBar {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  width: 85%;
  height: 60px;
}

#dashboardPage {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 0px;
  height: 70vh !important;
  padding: 30px;
  border-radius: 0;
  background-color: none;
}
.gridItem {
  background-color: rgba(255, 255, 255) !important;
  font-size: 1.3rem;
  padding: 10px;
  width: 45%;
  height: 48%;
  border-radius: 0 !important;
}
.sectionButton {
  width: 75%;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 5px !important;
  background-color: rgb(15, 15, 15) !important;
  color: rgb(255, 255, 255) !important;
}
#usersImage {
  background-image: url('../images/bg1.jpg');
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  height: 100%;
}
#projectsImage {
  background-image: url('../images/bg2.jpg');
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  height: 100%;
}
#ticketsImage {
  background-image: url('../images/bg3.jpg');
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  height: 100%;
}
#settingsImage {
  background-image: url('../images/bg4.jpg');
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  height: 100%;
}
.vertical-center {
  display: flex;
  align-items: center;
  justify-content: center;
  border: solid white 1px;
}
.padding-right-3rem {
  margin-right: .5rem !important;
}

.tableCell {
  padding: 10px !important;
}
.greyCell {
  background-color: rgb(134, 134, 134, .5) !important;
}

.noWrap {
  white-space: nowrap;
  width: min-content;
  border: solid 1px rgb(77, 77, 77) !important;
}
.LeftPadding {
  padding-left: 10px !important;
  padding-right: 15px !important;
}

.sidePadding {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.minWidthCell {
  width: 50px !important;
  border-bottom: solid rgb(77, 77, 77) 1px !important;
}

.viewButton {
  border: none !important;
  border-radius: 2px;
  background-color: #fefefe;
  box-shadow: 0px 2px 5px 2px rgb(173, 173, 173);
  padding: 10px 20px;
  cursor: pointer;
  color: rgb(14, 14, 14);
  font-weight: 600 !important;
}

.priorityLow {
  background-color: rgb(32, 110, 38);
  color: white !important;
}
.priorityMedium {
  background-color: rgb(180, 121, 44);
  color: rgb(255, 255, 255) !important;
}
.priorityHigh {
  background-color: rgb(187, 87, 51);
  color: white !important;
}

/*For displayTable information box*/
.itemHeader {
  font-weight: 700 !important;
  font-size: 1rem !important;
  text-decoration: underline;
  width: 20% !important;
}
.itemInfo {
  font-weight: 400 !important;
}
.smallCell {
  width: 20px !important;
}
.topCell {
  border-top: 0 !important;
}
.topLeftCell {
  border-top: 0 !important;
  border-left: 0 !important;
}
.sidebarList {
  background-color: rgb(0,0,0) !important;
}
.sidebarDivider {
  border: 1px solid rgb(0, 0, 0) !important;
  background-color: none;
}
#helloUser {
  background-color: rgb(0, 0, 0, .8) !important;
  color: white;
}
#sidebarIcon {
  color: white !important;
}
.sidebarButton {
  color: rgb(188, 97, 248) !important;
}
.sidebarText {
  color: rgb(255, 255, 255) !important;
}
.sidebarButton:hover {
  text-decoration: underline !important;
}

.appBar {
  background-color: rgba(0, 0, 0) !important;
  box-shadow: none !important;
  color: rgb(236, 236, 236) !important;
  font-family: 'Parisienne' !important;
  font-size: 3rem !important;
}

.wideForm {
  min-width: 550px !important;
  max-height: 100%;
}

#modalDivParent {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
}
#modalDiv {
  position: fixed;
  background-color: white;
  border: solid rgb(36, 36, 36) 2px;
  width: 550px;
  height: 1px;
  top: -500px;
  left: -500px;
}
.editsUL {
  background-color: rgb(226, 226, 226);
  border-radius: 2px;
  list-style-type: none;
  text-align: left;
  padding: 0px 10px;
  margin: 10px;
  height: 80%;
  overflow-y: scroll;
}
.editsLI {
  border: solid rgb(117, 117, 117) 1px;
  padding: 5px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  margin-top: 10px;
}
h3 {
  margin: 10px;
}
.change {
  font-weight: bold;
}
#checkBox {
  margin-top: auto;
  margin-bottom: auto;
}
#searchBarDiv {
  margin-top: auto;
  margin-bottom: auto;
}
#searchBar {
  -webkit-appearance: none;
  padding: 5px;
  border-radius: 2px;
  border: solid rgb(122, 122, 122) 1px;
}
#go {
  cursor: pointer;
  border-radius: 2px;
  border: solid 1px rgb(122, 122, 122);
  box-shadow: 0px 1px 5px 1px rgb(122, 122, 122);
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 5px;
  margin-right: 5px;
  padding: 5px 20px !important;
  font-family: inherit;
}

.dashboardTopBar {
  background-color: rgb(0, 0, 0, .8);
  margin: 4% 8%;
}

.settingsParent {
  background-color: white;
  margin: 4% 8%;
  padding-bottom: 20px;
  box-shadow: 0px 2px 10px 0px rgb(122, 122, 122);
}
#settingsDiv {
  background-color: rgb(223, 223, 223);
  width: 95%;
  height: 95%;
  margin: auto;
  border-radius: 0px;
  text-align: center;
  padding: 15px;
}

.innerDiv {
  background-color: rgb(255, 255, 255);
  padding: 10px;
  margin: 10px;
  border-radius: 2px;
  font-weight: bold;
  display: flex;
  justify-content: space-evenly;
}

.settingsInput {
  margin-left: 20px;
}

.colorSelect {
  display: inline-block;
  border: solid black 1px;
  padding: 5px;
  margin-top: auto;
  margin-bottom: auto;
  margin: 5px;
}
#colorSelect {
  margin-top: auto;
  margin-bottom: auto;
}
#settingsButton {
  margin-top: 15px;
}

.errorPage {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.errorH1 {
  background-color: rgb(0, 0, 0, .8);
  padding: 2rem;
  color: white;
}